import bn from 'bn.js';
import { PriKeyType, PriKeySerializeSize, PaymentAddressType, PaymentAddrSerializeSize, ViewingKeyType, ReadonlyKeySerializeSize, PublicKeyType, PublicKeySerializeSize, ChildNumberSize, ChainCodeSize, } from '@src/services/wallet/constants';
import { addChecksumToBytes } from './utils';
import { checkEncode, checkDecode, checkSumFirst4Bytes } from '@src/utils/base58';
import { ENCODE_VERSION, ED25519_KEY_SIZE } from '@src/constants/constants';
import PrivateKeyModel from '@src/models/key//privateKey';
import PaymentAddressKeyModel from '@src/models/key/paymentAddress';
import ViewingKeyModel from '@src/models/key//viewingKey';
import { generateKeySet, getBackupData as getBackupDataKeySet, restoreKeySetFromBackupData } from '@src/services/key/accountKeySet';
import CryptoJS from 'crypto-js';
import { wordArrayToByteArray, byteArrayToWordArray } from '@src/services/key/utils';
import KeyWalletModel from '@src/models/key/keyWallet';
export function serializeKey(key, depth, childNumber, chainCode) {
    // Write fields to buffer in order
    var keyBytes;
    var keyType = key.keyType;
    if (keyType === PriKeyType) {
        keyBytes = new Uint8Array(PriKeySerializeSize);
        var offset = 0;
        keyBytes.set([keyType], offset);
        offset += 1;
        keyBytes.set([depth], offset);
        offset += 1;
        keyBytes.set(childNumber, offset);
        offset += ChildNumberSize;
        keyBytes.set(chainCode, offset);
        offset += ChainCodeSize;
        keyBytes.set([key.privateKeyBytes.length], offset);
        offset += 1;
        keyBytes.set(key.privateKeyBytes, offset);
    }
    else if (keyType === PaymentAddressType) {
        keyBytes = new Uint8Array(PaymentAddrSerializeSize);
        var offset = 0;
        keyBytes.set([keyType], offset);
        offset += 1;
        keyBytes.set([key.publicKeyBytes.length], offset);
        offset += 1;
        keyBytes.set(key.publicKeyBytes, offset);
        offset += ED25519_KEY_SIZE;
        keyBytes.set([key.transmissionKeyBytes.length], offset);
        offset += 1;
        keyBytes.set(key.transmissionKeyBytes, offset);
    }
    else if (keyType === ViewingKeyType) {
        keyBytes = new Uint8Array(ReadonlyKeySerializeSize);
        var offset = 0;
        keyBytes.set([keyType], offset);
        offset += 1;
        keyBytes.set([key.publicKeyBytes.length], offset);
        offset += 1;
        keyBytes.set(key.publicKeyBytes, offset);
        offset += ED25519_KEY_SIZE;
        keyBytes.set([key.receivingKeyBytes.length], offset);
        offset += 1;
        keyBytes.set(key.receivingKeyBytes, offset);
    }
    else if (keyType === PublicKeyType) {
        keyBytes = new Uint8Array(PublicKeySerializeSize);
        var offset = 0;
        keyBytes.set([keyType], offset);
        offset += 1;
        keyBytes.set([key.publicKeyBytes.length], offset);
        offset += 1;
        keyBytes.set(key.publicKeyBytes, offset);
    }
    // Append key bytes to the standard sha3 checksum
    return addChecksumToBytes(keyBytes);
}
function deserializeKeyValidate(bytes) {
    // validate checksum
    var cs1 = checkSumFirst4Bytes(bytes.slice(0, bytes.length - 4));
    var cs2 = bytes.slice(bytes.length - 4);
    if (!cs1.equals(cs2)) {
        throw Error('Checksum deserialize key wrong!!!');
    }
}
export function deserializePrivateKeyBytes(bytes) {
    var depth = bytes[1];
    var childNumber = bytes.slice(2, 6);
    var chainCode = bytes.slice(6, 38);
    var keyLength = bytes[38];
    var privateKey = new PrivateKeyModel(bytes.slice(39, 39 + keyLength));
    deserializeKeyValidate(bytes);
    return {
        depth: depth,
        childNumber: childNumber,
        chainCode: chainCode,
        privateKey: privateKey
    };
}
export function deserializePaymentAddressKeyBytes(bytes) {
    var paymentAddress = new PaymentAddressKeyModel();
    var publicKeyLength = bytes[1];
    paymentAddress.publicKeyBytes = bytes.slice(2, 2 + publicKeyLength);
    var transmisionKeyLength = bytes[publicKeyLength + 2];
    paymentAddress.transmissionKeyBytes = bytes.slice(publicKeyLength + 3, publicKeyLength + 3 + transmisionKeyLength);
    deserializeKeyValidate(bytes);
    return paymentAddress;
}
export function deserializeViewingKeyBytes(bytes) {
    var viewingKey = new ViewingKeyModel();
    var publicKeyLength = bytes[1];
    viewingKey.publicKeyBytes = bytes.slice(2, 2 + publicKeyLength);
    var receivingKeyLength = bytes[publicKeyLength + 2];
    viewingKey.receivingKeyBytes = bytes.slice(publicKeyLength + 3, publicKeyLength + 3 + receivingKeyLength);
    deserializeKeyValidate(bytes);
    return viewingKey;
}
export function deserializePublicKeyBytes(bytes) {
    var publicKeyLength = bytes[1];
    var publicKeyBytes = bytes.slice(2, 2 + publicKeyLength);
    deserializeKeyValidate(bytes);
    return publicKeyBytes;
}
export function base58CheckSerialize(key, depth, childNumber, chainCode) {
    var serializedKey = serializeKey(key, depth, childNumber, chainCode);
    return checkEncode(serializedKey, ENCODE_VERSION);
}
export function getKeyTypeFromKeyBytes(keyBytes) {
    var keyType = keyBytes[0];
    return keyType;
}
export function base58CheckDeserialize(keyStr) {
    var keyBytes = checkDecode(keyStr).bytesDecoded;
    var keyType = getKeyTypeFromKeyBytes(keyBytes);
    var type;
    var key;
    switch (keyType) {
        case PriKeyType:
            type = 'PRIVATE_KEY';
            key = deserializePrivateKeyBytes(keyBytes);
            break;
        case PaymentAddressType:
            type = 'PAYMENT_ADDRESS';
            key = deserializePaymentAddressKeyBytes(keyBytes);
            break;
        case ViewingKeyType:
            type = 'VIEWING_KEY';
            key = deserializeViewingKeyBytes(keyBytes);
            break;
        case PublicKeyType:
            type = 'PUBLIC_KEY';
            key = deserializePublicKeyBytes(keyBytes);
            break;
    }
    return { type: type, key: key };
}
export function getIntermediary(childIndex, keyWalletChainCode) {
    var childIndexBytes = (new bn(childIndex)).toArray();
    // HmacSHA512(data, key)
    var hmac = CryptoJS.HmacSHA512(CryptoJS.enc.Base64.stringify(byteArrayToWordArray(keyWalletChainCode)), byteArrayToWordArray(Uint8Array.from(childIndexBytes)));
    var intermediary = wordArrayToByteArray(hmac);
    return intermediary;
}
export function generateChildKeyData(childIndex, keyWalletDepth, keyWalletChainCode) {
    var intermediary = getIntermediary(childIndex, keyWalletChainCode);
    var newSeed = intermediary.slice(0, 32);
    var keySet = generateKeySet(newSeed);
    var childNumber = Uint8Array.from((new bn(childIndex)).toArray('be', ChildNumberSize));
    var chainCode = Uint8Array.from(intermediary.slice(ChainCodeSize));
    return {
        childNumber: childNumber,
        chainCode: chainCode,
        depth: keyWalletDepth + 1,
        keySet: keySet
    };
}
export function getBackupData(keyWallet) {
    var data = {
        chainCode: Array.from(keyWallet.chainCode),
        childNumber: Array.from(keyWallet.childNumber),
        depth: keyWallet.depth,
        keySet: getBackupDataKeySet(keyWallet.keySet)
    };
    return data;
}
export function restoreKeyWalletFromBackupData(data) {
    var chainCode = data.chainCode, childNumber = data.childNumber, depth = data.depth, keySet = data.keySet;
    var keyWallet = new KeyWalletModel();
    keyWallet.chainCode = Uint8Array.from(chainCode);
    keyWallet.childNumber = Uint8Array.from(childNumber);
    keyWallet.depth = depth;
    keyWallet.keySet = restoreKeySetFromBackupData(keySet);
    return keyWallet;
}
